body {
  font-family: 'Roboto Condensed', sans-serif;
}

.searchBar {
  margin-top: 50px;
  padding: 10px;
}

input[type=text] {
  border: none;
  border-bottom: 1px solid #4ABDAC;
  margin-right: 20px;
  font-size: 18px;
  padding: 5px;
}

input[type=text]:focus {
  outline: none;
}

.btn {
  cursor: pointer;
  border: 2px solid transparent;
  background-color: #4ABDAC;
  border-radius: 10px;
  padding: 5px 20px;
  color: #fff;
  font-size: 18px;
  
}

.btn:hover {
  color: #4ABDAC;
  font-weight: 500;
  border: 2px solid #4ABDAC;
  background-color: #fff;
}

.weather-card {
  width: 15%;
  border: 1px solid #4ABDAC;
  border-radius: 15px;
  margin: 10px;
  padding: 5px 10px;
  margin-top: 50px;
  transition: linear 0.4s;
  height: 160px;
}

.weather-card:hover {
  background-color: #4ABDAC;
  cursor: pointer;
  transform:  scale(1.1);
  border: 1px solid #4ABDAC;
  color: #fff;
}

.title {
  padding: 10px 15px;
  border-radius: 10px;
  background-color: #FC4A1A;
  color: #fff;
  font-size: 30px;
  font-weight: 300;
}

.temperature {
font-size: 28px;
color: #373737;
}




